'use client';
import React, { useRef, useEffect, useState } from 'react';
import { SketchPicker } from 'react-color';

export default function WhiteboardCanvas() {
    const canvasRef = useRef(null);
    const [context, setContext] = useState(null);
    const [drawing, setDrawing] = useState(false);
    const [currentColor, setCurrentColor] = useState('#000000');
    const [lineWidth, setLineWidth] = useState(3);
    const [drawingActions, setDrawingActions] = useState([]);
    const [currentPath, setCurrentPath] = useState([]);
    const [currentStyle, setCurrentStyle] = useState({ color: '#000000', lineWidth: 3, brush: 'solid' });
    const [shape, setShape] = useState(null); // Default to freehand drawing
    const [text, setText] = useState('');
    const [fontSize, setFontSize] = useState(20);
    const [showTextInput, setShowTextInput] = useState(false);
    const [startPoint, setStartPoint] = useState(null);
    const [colorPickerVisible, setColorPickerVisible] = useState(false);
    const [showGrid, setShowGrid] = useState(false);

    useEffect(() => {
        const canvas = canvasRef.current;
        if (canvas) {
            canvas.width = 1300;
            canvas.height = 500;
            const ctx = canvas.getContext('2d');
            setContext(ctx);
            reDrawPreviousData(ctx);
        }
    }, [drawingActions]);

    const startDrawing = (e) => {
        if (context) {
            setStartPoint({ x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY });
            setDrawing(true);
            if (shape === null) { // Freehand drawing
                context.beginPath();
                context.moveTo(e.nativeEvent.offsetX, e.nativeEvent.offsetY);
            }
        }
    };

    const draw = (e) => {
        if (!drawing || !context) return;
        const x = e.nativeEvent.offsetX;
        const y = e.nativeEvent.offsetY;

        // Clear only the current drawing area, not the entire canvas
        context.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
        if (showGrid) drawGrid(context);
        reDrawPreviousData(context);

        context.strokeStyle = currentStyle.color;
        context.lineWidth = currentStyle.lineWidth;

        if (shape) {
            switch (shape) {
                case 'rectangle':
                    context.strokeRect(startPoint.x, startPoint.y, x - startPoint.x, y - startPoint.y);
                    break;
                case 'circle':
                    const radius = Math.sqrt(Math.pow(x - startPoint.x, 2) + Math.pow(y - startPoint.y, 2));
                    context.beginPath();
                    context.arc(startPoint.x, startPoint.y, radius, 0, 2 * Math.PI);
                    context.stroke();
                    break;
                case 'line':
                    context.beginPath();
                    context.moveTo(startPoint.x, startPoint.y);
                    context.lineTo(x, y);
                    context.stroke();
                    break;
                default:
                    break;
            }
        } else if (text) {
            context.font = `${fontSize}px Arial`;
            context.fillStyle = currentStyle.color;
            context.fillText(text, x, y);
        } else {
            // Freehand drawing with brush style
            context.beginPath();
            if (currentStyle.brush === 'solid') {
                context.lineCap = 'round';
                context.lineJoin = 'round';
            } else if (currentStyle.brush === 'dotted') {
                context.setLineDash([5, 15]);
            } else if (currentStyle.brush === 'dashed') {
                context.setLineDash([10, 10]);
            }
            context.lineTo(x, y);
            context.stroke();
            setCurrentPath([...currentPath, { x, y }]);
        }
    };

    const endDrawing = (e) => {
        setDrawing(false);
        if (startPoint && shape) {
            setDrawingActions([...drawingActions, { shape, startPoint, endPoint: { x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY }, style: currentStyle }]);
            setStartPoint(null);
        } else if (text) {
            setDrawingActions([...drawingActions, { text, position: { x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY }, style: currentStyle }]);
            setText('');
            setShowTextInput(false);
        } else if (currentPath.length > 0) {
            setDrawingActions([...drawingActions, { path: currentPath, style: currentStyle }]);
        }
        setCurrentPath([]);
    };

    const changeColor = (color) => {
        setCurrentColor(color.hex);
        setCurrentStyle({ ...currentStyle, color: color.hex });
        setColorPickerVisible(false);
    };

    const changeWidth = (width) => {
        setLineWidth(width);
        setCurrentStyle({ ...currentStyle, lineWidth: width });
    };

    const changeBrush = (brushType) => {
        setCurrentStyle({ ...currentStyle, brush: brushType });
    };

    const changeShape = (shapeType) => {
        setShape(shapeType);
    };

    const undoDrawing = () => {
        if (drawingActions.length > 0) {
            const updatedActions = [...drawingActions];
            updatedActions.pop();
            setDrawingActions(updatedActions);
        }
    };

    const clearDrawing = () => {
        setDrawingActions([]);
        setCurrentPath([]);
        const newContext = canvasRef.current.getContext('2d');
        newContext.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
    };

    const reDrawPreviousData = (ctx) => {
        drawingActions.forEach(({ shape, startPoint, endPoint, style, text, position, path }) => {
            ctx.strokeStyle = style.color;
            ctx.lineWidth = style.lineWidth;

            if (style.brush === 'solid') {
                ctx.lineCap = 'round';
                ctx.lineJoin = 'round';
                ctx.setLineDash([]);
            } else if (style.brush === 'dotted') {
                ctx.setLineDash([5, 15]);
            } else if (style.brush === 'dashed') {
                ctx.setLineDash([10, 10]);
            }

            if (shape) {
                switch (shape) {
                    case 'rectangle':
                        ctx.strokeRect(startPoint.x, startPoint.y, endPoint.x - startPoint.x, endPoint.y - startPoint.y);
                        break;
                    case 'circle':
                        const radius = Math.sqrt(Math.pow(endPoint.x - startPoint.x, 2) + Math.pow(endPoint.y - startPoint.y, 2));
                        ctx.beginPath();
                        ctx.arc(startPoint.x, startPoint.y, radius, 0, 2 * Math.PI);
                        ctx.stroke();
                        break;
                    case 'line':
                        ctx.beginPath();
                        ctx.moveTo(startPoint.x, startPoint.y);
                        ctx.lineTo(endPoint.x, endPoint.y);
                        ctx.stroke();
                        break;
                    default:
                        break;
                }
            } else if (text) {
                ctx.font = `${fontSize}px Arial`;
                ctx.fillStyle = style.color;
                ctx.fillText(text, position.x, position.y);
            } else if (path) {
                ctx.beginPath();
                if (style.brush === 'solid') {
                    ctx.lineCap = 'round';
                    ctx.lineJoin = 'round';
                } else if (style.brush === 'dotted') {
                    ctx.setLineDash([5, 15]);
                } else if (style.brush === 'dashed') {
                    ctx.setLineDash([10, 10]);
                }
                path.forEach((point, index) => {
                    if (index === 0) {
                        ctx.moveTo(point.x, point.y);
                    } else {
                        ctx.lineTo(point.x, point.y);
                    }
                });
                ctx.stroke();
            }
        });
    };

    const drawGrid = (ctx) => {
        const spacing = 20;
        ctx.strokeStyle = '#ddd';
        ctx.lineWidth = 0.5;
        for (let x = 0; x < canvasRef.current.width; x += spacing) {
            ctx.beginPath();
            ctx.moveTo(x, 0);
            ctx.lineTo(x, canvasRef.current.height);
            ctx.stroke();
        }
        for (let y = 0; y < canvasRef.current.height; y += spacing) {
            ctx.beginPath();
            ctx.moveTo(0, y);
            ctx.lineTo(canvasRef.current.width, y);
            ctx.stroke();
        }
    };

    const handleSave = () => {
        const canvas = canvasRef.current;
        const link = document.createElement('a');
        link.download = 'whiteboard.png';
        link.href = canvas.toDataURL();
        link.click();
    };

    return (
        <div>
            <h1 style={{ textAlign: 'center', marginBottom: '20px' }}>Whiteboard Canvas</h1>
            <canvas
                ref={canvasRef}
                onMouseDown={startDrawing}
                onMouseMove={draw}
                onMouseUp={endDrawing}
                onMouseLeave={endDrawing}
                style={{ border: '1px solid #000' }}
            />
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: '10px' }}>
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={undoDrawing}
                >
                    Undo
                </button>
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={clearDrawing}
                >
                    Clear
                </button>
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={handleSave}
                >
                    Save
                </button>
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={() => setColorPickerVisible(!colorPickerVisible)}
                >
                    Pick Color
                </button>
                {colorPickerVisible && (
                    <div style={{ position: 'absolute', zIndex: 2 }}>
                        <SketchPicker
                            color={currentColor}
                            onChangeComplete={changeColor}
                        />
                    </div>
                )}
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={() => setShowGrid(!showGrid)}
                >
                    Add Grid
                </button>
            </div>
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: '10px' }}>
                <label>
                    Width:
                    <input
                        type="range"
                        min="1"
                        max="20"
                        value={lineWidth}
                        onChange={(e) => changeWidth(e.target.value)}
                        style={{ margin: '0 10px' }}
                    />
                </label>
                <label>
                    Brush:
                    <select
                        value={currentStyle.brush}
                        onChange={(e) => changeBrush(e.target.value)}
                        style={{ margin: '0 10px' }}
                    >
                        <option value="solid">Solid</option>
                        <option value="dotted">Dotted</option>
                        <option value="dashed">Dashed</option>
                    </select>
                </label>
                <label>
                    Shape:
                    <select
                        value={shape}
                        onChange={(e) => changeShape(e.target.value)}
                        style={{ margin: '0 10px' }}
                    >
                        <option value={null}>Freehand</option>
                        <option value="rectangle">Rectangle</option>
                        <option value="circle">Circle</option>
                        <option value="line">Line</option>
                    </select>
                </label>
                <button
                    style={{ margin: '0 10px', padding: '10px 20px', borderRadius: '5px', cursor: 'pointer' }}
                    onClick={() => setShowTextInput(!showTextInput)}
                >
                    Add Text
                </button>
                {showTextInput && (
                    <div>
                        <input
                            type="text"
                            value={text}
                            onChange={(e) => setText(e.target.value)}
                            placeholder="Enter text"
                            style={{ margin: '0 10px' }}
                        />
                        <label>
                            Font Size:
                            <input
                                type="number"
                                value={fontSize}
                                onChange={(e) => setFontSize(e.target.value)}
                                style={{ margin: '0 10px' }}
                            />
                        </label>
                    </div>
                )}
            </div>
        </div>
    );
}
